.left-side-about {
    right: 0;
    margin-right: 30px;
    position: absolute;
    display: block;
    width: 50%;
    height: 80%;
    top: 10%;
    border: 1px solid #e0e0e0;


}

.container.about-page {
    border: 1px solid #e1e4e8;
    img {
        right: 58%;
        top: 30px;
        border: 1px solid #e1e4e8;
        overflow: hidden;
        width: 200px;
        height: 200px;
        object-fit: cover;
        border-radius: 50%;
        position: absolute;
    }
}

