.nav-bar {
    background: black;
    width: 60px;
    height: 100%;
    position: absolute;
    z-index: 3;
    top : 0;
    min-height: 500px;

    .logo {
        display: block;
        padding: 8px, 0; 

        img {
            display: block;
            margin: 8px auto;
            width: 24px;
            height: auto;

            &.sub-logo {
                width: 50px;
            }
        }
    }

    nav {
        display: block;
        text-align: center;
        position: absolute;
        height: 210px;
        top: 50%;
        margin-top: -120px;
        width: 100%;

        a {
            font-size: 22px;
            color : #4d4d4d;
            display: block;
            line-height: 51px;
            height: 51px;
            position: relative;
            text-decoration: none;

            i {
                transition: all 0.3s ease-out;
            }

            &:hover {                
                svg {
                    color: #dc143c;
                }
            }

            &:after {
                content : '';
                font-size: 9px;
                letter-spacing: 2px;
                position: absolute;
                bottom: 0;
                display: block;
                width: 100%;
                text-align: center;
                opacity: 0;
                -webkit-transition: all 0.3s ease-out;
                transition: all 0.3s ease-out;
            } 
        }
    }

    ul {
        position: absolute;
        bottom: 20px;
        width: 100%;
        display: block;
        padding: 0;
        list-style: none;
        text-align: center;
        margin: 0;

        li {
            a {
                padding: 7px 0;// top bottom
                display: block;
                font-size: 15px;
                line-height: 16px;
                color: #4d4d4d;

                &:hover svg{
                    color: #dc143c;
                }
            }
        }

    }
}

